    calculateZodiac(birthYear) {
      if (!birthYear) return "";

      // æå–å¹´ä»½æ•°å­—
      let year;
      if (typeof birthYear === 'string') {
        const match = birthYear.match(/\d{4}/);
        if (match) {
          year = parseInt(match[0]);
        } else {
          return "";
        }
      } else if (typeof birthYear === 'number') {
        year = birthYear;
      } else {
        return "";
